export const required = value => value ? undefined : '        ';
export const selectRequired = value => value ? undefined : '   ';

export const regex_email = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i;
// '      ,         .';
export const firstMiddleLastNames = /^[A-Z-][a-z-]*$/i;
// '    ,  "-",      a-z,  -.';
export const regex_name = /^[a-zA-Z--\s\-]*$/i;
// '     ,      .';
export const regex_houseNum = /^[\w0-9]*$/i;
export const regex_passport = /^[A-Z-0-9\-]*$/i;
  // '      ,  "-"         -, A-Z.';
export const regex_birthday = /^([0-2][0-9]|3[0-1])\/(0[0-9]|1[0-2])\/([1-2][019][0-9][0-9])$/i;
  // '   0  9.      (),  ,      "-".';
export const regex_phone = /^\+?[0-9\s\-)(]*$/i;
// '        a-z,  -,   ,         "-".';
export const regex_full_name = /^\+?([A-Za-z---\-]*\s)([A-Za-z---\-]*)(\s[A-Za-z---\-]*)?$/i;

// '  ,    .';
export const regex_number =/^\+?([0-9]*).?([0-9]{1,2})?$/i;


const createMessage = (nameOfSmth, min, max) => (
  ` ${nameOfSmth}     ${min}     ${max}.`
);
const createLengthMessage = (nameOfSmth, min, max) => (
  `  ${nameOfSmth}  ${min} .   ${max} .`
);

export const cityLength = value => value && !(value.length < 3) ? undefined :
  createMessage('', 3, 50);
export const regionLength = value => value && !(value.length < 5) ? undefined :
  createMessage('', 5, 50);
export const streetLength = value => value && !(value.length < 3) ? undefined :
  createMessage('', 3, 50);

export const fullNameLength = value => value && value.length < 150 && value.length > 5 ? undefined :
  createLengthMessage(' ', 5, 150);
export const nameLength = value => value && !(value.length < 2) ? undefined :
  createLengthMessage('', 2, 24);
export const lastNameLength = value => value && value.length >= 2 ? undefined :
  createLengthMessage('', 2, 30);	
export const middleNameLength = value => value && value.length >= 2 ? undefined :
  createLengthMessage('', 2, 30);	
export const houseNumLength = value => value && value.length > 0 ? undefined :
  createLengthMessage(' ', 1, 7);
export const passportLength = value => value && value.length > 6 ? undefined :
  createLengthMessage('', 7, 15);
export const phoneLength = value => value && value.length > 0 ? undefined :
  createLengthMessage(' ', 11, 24);



