<template>
    <div class="mainContent">
    	<section @mouseup="onMouseUpParentAwesomeButton" class="banner parent h-around v-centered prespective wrap">
    		<h1 class="bold banner__title textShadow">
    			<span class="mainColor">  20%</span>  <span class="mainColor"></span>   <span class="mainColor">.</span></h1>
    		<awesome-link label="   "
    				content="" href="/registration" className="banner__button whiteTextShadow"></awesome-link>
			
    		<blurry-image-loader
    			:src="banner"
    			defaultMedia="(min-width: 481px)"
    			alt=""
    			modifier="banner"
    			className="materialShadow fewRound fullWidth"
    		/>

    	</section>
    	<fade-translate-transition-group tag="section" className="h-between blog parent row wrap"
    	:duration="1500">
			<about-item
				v-for="aboutItem in aboutItems" 
				:key="aboutItem.id"
				:data-index="aboutItem.id"
				:title="aboutItem.title"
				:paragraph="aboutItem.paragraph">
			</about-item>
		</fade-translate-transition-group>
		<section class="programm parent column">
			<decorative-title>  </decorative-title>

			<p :class="['programm__paragraph', paragraph.position]"
				v-for="paragraph in programmParagraphs"
				:key="paragraph.id">
				{{ paragraph.text }}
			</p>
			<div class="programmItems parent h-between row wrap">
				
				<programm-item 
					:src="item.src"
					:title="item.title"
					:key="item.id"
					:name="item.name"
					:stepDescription="item.stepDescription"
					:titleModifier="item.titleModifier"
					v-for='item in programmItems'/>
			</div>
			
		</section>
		<section class="extraInformation parent row wrap">
			<decorative-title className="extraInformation__title container"> </decorative-title>
			<div class="container currentCatalog baseChild halfWidth">
				<h3 class="currentCatalog__title"> </h3>
				<external-link to="https://ru.oriflame.com/ecatalogue/502181?per=201609" showIcon="hide" className="materialShadow currentCatalog__image">
					<blurry-image-loader 
						relative
						alt=" "
						src="https://ru.oriflame.com/catalogue-image.png"/>
				</external-link>
			</div>

			<ul role="navigation" aria-label="   " class="container baseChild extraInformation__navigation">
				<li :key="link.id" v-for="link in extraInfoLinks">
					<internal-link
						:to="link.href" 
						:hashResource="link.hashResource"
						v-if="!link.external"
						className="darkenHover"
					>
						{{ link.text }}
					</internal-link>
					<external-link 
						className="darkenHover"
						:to="link.href" 
						v-if="link.external">
						{{ link.text }}
					</external-link>
					
				</li>
			</ul>
		</section>
    </div>
</template>

<script>
	// Images
	import banner from './../assets/images/banner.png';
	import mobileBanner from './../assets/images/mobile-banner.png';
	import firstStepImage from './../assets/images/programm/first.png'
	import secondStepImage from './../assets/images/programm/second.png'
	import thirdStepImage from './../assets/images/programm/third.png'
	import fourthWoomanStepImage from './../assets/images/programm/fourth_wooman.png'
	import fourthManStepImage from './../assets/images/programm/fourth_man.png'
 
	// Components
	import FadeTranslateTransitionGroup from '@/components/FadeTranslateTransitionGroup';
	import BlurryImageLoader from '@/components/BlurryImageLoader';
	import AwesomeLink from '@/components/AwesomeLink';
	import AboutItem from '@/components/AboutItem';
	import ExternalLink from '@/components/ExternalLink';
	import InternalLink from '@/components/InternalLink';
	import DecorativeTitle from '@/components/DecorativeTitle';
	import ProgrammItem from '@/components/ProgrammItem';


	import {onMouseUpParentAwesomeButton} from './../constants/pureFunctions';

	export default {
	  name: 'MainPage',
	  components: {
	  	BlurryImageLoader,
	  	AwesomeLink,
	  	AboutItem,
	  	DecorativeTitle,
	  	ProgrammItem,
	  	ExternalLink,
	  	InternalLink,
	  	FadeTranslateTransitionGroup
	  },
	  data() {
	  	return {
	  		banner: window.innerWidth <= 480 ? 
	  			mobileBanner : 
	  			banner,
	  		aboutItems: [
	  			{
	  				id: 1,
	  				title: '',
	  				paragraph: '   ,        .'
	  			},
	  			{
	  				id: 2,
	  				title: '',
	  				paragraph: '      ,    ,  ,   .'
	  			},
	  			{
	  				id: 3,
	  				title: ' ',
	  				paragraph: '          .           .'
	  			},
	  			{
	  				id: 4,
	  				title: '',
	  				paragraph: '         .  ,        .'
	  			}
	  		], // end aboutItems
	  		programmParagraphs: [
	  			{
	  				id: 1,
	  				text: ',    .     .   ,    ,    .',
	  				position: 'v-s-start'
	  			},{
	  				id: 2,
	  				text: '    .            .',
	  				position: 'v-s-centered'
	  			},{
	  				id: 3,
	  				text: ' 200 ,      ,     .   ,      !',
	  				position: 'v-s-end'
	  			}
	  		], // end aboutParagraphs
	  		programmItems: [
	  			{
	  				id: 0,
	  				title: " ",
	  				src: firstStepImage,
	  				name: "first",
	  				stepDescription: {
	  					paragraph: "   200 ,        .   1033 + 5%    .",
	  					items: [
	  						{
	  							name: '     ',
	  							id: 0
	  						},
	  						{
	  							name: '-       (: 31604)',
	  							id: 1
	  						},
	  						{
	  							name: '          (: 31605)',
	  							id: 2
	  						},
	  						{
	  							name: '     (: 1276)',
	  							id: 3
	  						},
	  						
	  					]
	  				}

	  			}, // end first
	  			{
	  				id: 1,
	  				title: " ",
	  				src: secondStepImage,
	  				name: "second",
	  				titleModifier: 'right',
	  				stepDescription: {
	  					paragraph: "   ,        : 2066 + 5%   .",
	  					items: [
	  						{
	  							name: '  The ONE Colour Stylist (  )',
	  							id: 0
	  						},
	  						{
	  							name: '   5--11 The ONE Wonderlash ( )',
	  							id: 1
	  						},
	  					]
	  				}

	  			}, // end second
	  			{
	  				id: 2,
	  				title: " ",
	  				src: thirdStepImage,
	  				name: "third",
	  				stepDescription: {
	  					paragraph: "  ,             3099 + 5%   . ",
	  					items: [
	  						{
	  							name: ' Optimals Age Revive:     ,        (: 540211)',
	  							id: 0
	  						},
	  						{
	  							name: '  Eclat Femme  (: 540212)  Eclat Homme   (: 540213)',
	  							id: 2
	  						},
	  					]
	  				}

	  			}, // end third
	  			{
	  				id: 3,
	  				name: "fourthMan",
	  				src: fourthManStepImage,
	  				title: "   ",
	  				titleModifier: 'center',
	  				stepDescription: {
	  					paragraph: "    ,       4 130 + 5%   .",
	  					items: [
	  						{
	  							name: '    (: 540215)',
	  							id: 0
	  						}
	  					]
	  				}

	  			}, // end fourthWooman
	  			{
	  				id: 4,
	  				title: "   ",
	  				src: fourthWoomanStepImage,
	  				name: "fourthWooman",
	  				titleModifier: 'center',
	  				stepDescription: {
	  					paragraph: "   ,          4 130 + 5%   .",
	  					items: [
	  						{
	  							name: '    (: 540214)',
	  							id: 0
	  						}
	  						
	  					]
	  				}

	  			} // end fourthMan
	  		], // end programmItems
	  		extraInfoLinks: [
	  			{
	  				id: 0,
	  				text: " ",
	  				href: "/business",
	  				hashResource:  '#scores'
	  			},
	  			{
	  				id: 1,
	  				text: " ",
	  				href: "/registration",
	  				hashResource:  '#description'
	  			},
	  			{
	  				id: 2,
	  				text: " ",
	  				href: "/shares",
	  				external: false  
	  			},
	  			{
	  				id: 3,
	  				text: " ",
	  				href: "https://shining-present.ru",
	  				external: true  
	  			},
	  			{
	  				id: 4,
	  				text: "  ",
	  				href: "/business",

	  			},
	  			{
	  				id: 5,
	  				text: "   ",
	  				href: "/media"
	  			},
	  		] // end extraInfoLinks
	  	};
	  },
	  methods: {
	  	onMouseUpParentAwesomeButton
	  }
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='sass'>
	// Variables and helpful functions
	@import './../styles/conf/_sizes.sass'
	@import './../styles/conf/_colors.sass'
	@import './../styles/conf/_breakpoints.sass'
	
	.banner
		position: relative
		z-index: 0
		padding: .8rem 1.5rem 2rem
		color: $white
		background-color: rgba(51, 51, 51, .4)
		.progressive-image 
			position: absolute $i
		&__title
			@include breakpoint($sm-up)
			    max-width: 75%;
			@include breakpoint($xs)
				margin-bottom: 1.5rem;
		& a
			margin-top: 1.5rem
		&__button
			color: $darkGray
			
	.imageContainer_banner
		position: absolute
		top: 0
		z-index: -1
		left: 0
		.progressive-image-main,.progressive-image-wrapper
			height: 100%
	.imageContainer_banner
		// max-height: em(166)
		height: 100%
	.programm
		margin-top: em(77 - 29.124) $i
		&__paragraph
			margin-top: $s47 $i
			max-width: em(522.54)
	
	.programmItems
		margin-top: $s77
	.currentCatalog
		@include breakpoint($xxs)
			order: 2
			margin-top: $s47
		&__title
			margin-bottom: $s16
	.extraInformation
		
		&__title
			margin-bottom: $s47
			.decorativeTitleContainer__title
				@include breakpoint($xxs)
					min-width: 80%

		&__navigation
			padding-top: em(39)
			max-width: 25%
			min-width: 45%
			@include breakpoint($xxs)
				max-width: none
		&__image
			min-height: em(500)
	.extraInformation__title, .currentCatalog, .extraInformation__navigation
		@include breakpoint($xxs)
			padding: 0
	.currentCatalog, .extraInformation__navigation
		@include breakpoint($xxs)
			min-width: 100%
</style>



